import React from 'react';
import { NavLink } from 'react-router-dom';
import AppBar from '@material-ui/core/AppBar';

const Menu = () => {
    return (
        <>
            <div id="appbar">
                <NavLink className="items" to="/"> About </NavLink>
                <NavLink className="items" to="/contact"> Contact </NavLink>
                <NavLink className="items" to="/search"> Search </NavLink>
                <NavLink className="items" to="/user"> User </NavLink>
            </div>
        </>
    )
};

export default Menu;