import React, { useState } from 'react';
import Sresult from './Sresult';

const Search = () => {
    const [img, setImg] = useState();
    const inputEvent = (e) => {
        setImg(e.target.value);
    };
    return (
        <>
            <input id="search" type="search" placeholder="Search something" value={img} onChange={inputEvent} />
            <Sresult img={img} />
        </>
    );
};

export default Search;