import React from 'react';

const Sresult = (props) => {
    const img = `https://source.unsplash.com/600x400/?${props.img}`;
    return (
        <>
            <div id="sresult">
                <img src={img} alt="search" width="95%" />
            </div>
        </>
    )
};

export default Sresult;